.mdlext-aria-expanded-more-less {
  @include mdlext-aria-toggle($font-size: 24px, $icon: 'expand_more', $icon-expanded: 'expand_less');
}

.mdlext-menu-button {
  @include typo-menu();
  text-transform: none;
  height: $button-height;
  padding: 0 $button-padding;
  display: inline-block;
  line-height: $button-height;
  vertical-align: middle;

  &__label {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}


.mdlext-menu,
.mdlext-menu__item {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.mdlext-menu {
  position: absolute;
  z-index: 1000;
  min-width: 100px;
  max-width: 480px;
  border-radius: 2px;
  @include shadow-3dp();
  display: inline-flex;
  flex-direction: column;
  padding: 4px 0;
  color: $default-item-text-color;
  background-color: $default-dropdown-bg-color;

  &:focus {
    outline-offset: -1px;
    outline-width: 1px;
  }

  &[hidden] {
    @include mdlext-visually-hidden;
    pointer-events: none;
  }

  &__item {
    padding: 0 8px;
    @include typo-body-1();
    align-items: center;
    align-self: stretch;
    text-decoration: none;
    cursor: pointer;
    white-space: nowrap;
    user-select: none;
    min-height: 40px;
    overflow: hidden;

    &:active,
    &[aria-selected='true'] {
      background-color: $default-item-active-bg-color;
    }

    &:hover:not([disabled]) {
      background-color: $default-item-hover-bg-color;
    }

    &:focus {
      outline-offset: -1px;
      outline-width: 1px;
      background-color: $default-item-focus-bg-color;
    }

    &::-moz-focus-inner {
      border: 0;
    }

    &[disabled] {
      color: $disabled-item-text-color;
      background-color: transparent;
      cursor: auto;
      pointer-events: none;

      > * {
        color: $disabled-item-text-color;
      }
    }

    &__label {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    > * {
      margin: 0;
      padding: 0 0 0 8px;
    }

    > *:first-child {
      padding-left: 0;
    }

    > *:last-child:not(:only-child):not(.mdlext-menu__item__label) {
      margin-left: auto; // If more than one element, push last element to the right
    }

  }

  &__item-divider {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border-bottom: 1px solid $default-item-divider-color;
  }
}



